import ILogEntry from './ILogEntry';
import ILogSource from './ILogSource';

/**
 * @internal
 */
interface ITraceLogger {
  logError(source: ILogSource, error: Error, eventName?: string, resultCode?: string): void;
  logVerbose(source: ILogSource, message: string, eventName?: string): void;
  logErrorWithLogEntry(source: ILogSource, logEntry: ILogEntry,
    eventName?: string, resultCode?: string): void;
  logVerboseWithLogEntry(source: ILogSource, logEntry: ILogEntry, eventName?: string): void;
}

export default ITraceLogger;
